*{padding: 0; margin: 0;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
 display: flex;
 justify-content: space-between;
 background-color: #ffffff;
 align-items: center;
 padding: 10px;
 position: sticky;
 top: 0;
 z-index: 100;
 
}

.h_left>img{
  width:80px;
  height:24px;
  object-fit: contain;
  margin-left: 20px;

}
.h_middle>input{
  flex: 1;
  padding: 5px;
  border: none;
  outline-width:0;
  

}
.hm_icon{
  width: 50px !important;
  background-color: #f8f8f8;
  border-left: 1px solid lightgray;
}

.h_middle{
  display: flex;
  align-items: center;
  width: 40%;
  border: 1px solid lightgray;
}
.h_right{
  display: flex;
}

.hr_icon{
  margin-right:10px;
}


.app_sv{
display: flex;
}
.sidebar{
  flex: 0.2;
 
}


.row{
  display: flex;
  align-items: center;
  padding: 10px 20px;
}
.row_icon{
 color: #606060;
 font-size: large !important;
}
.row_title{
  flex: 1;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
}
.row:hover{
  background-color: lightgray;
  cursor: pointer;
}

.row:hover>.row_icon{
  color: red;
}
.row:hover>.row_title{
  font-weight: bold;
}
.clicked{
  background-color:lightgray;
}
.clicked>.row_icon{
  color: red;
}
.clicked>.row_title{
  font-weight: bold;
}

.sidebar>hr{
  height: 1px;
  border: 0;
  background-color: lightgray;
  margin-top: 10px;
  margin-bottom: 10px;
}
.sidebar>h4{
  display: flex;
  padding: 10px 20px;
  color: #606060;
  font-size: 15px;
  font-weight: 700;
}
.sidebar>p{
  display: flex;
  padding: 10px 20px;
  color: #606060;
  font-size: 15px;
  font-weight: 700;
  line-height: 20px;
}
.sidebar>span{
  display: flex;
  padding:40px 20px;
  font-size: 14px;
  align-items: center;
  font-weight: 500;
  color: #606060;
}
.span_icon{
  margin-right:5px;
  font-size: 15px !important;
}
.video{
  flex: 0.8;
  padding:0 20px ;
  padding-top:30px;
}

.play{
  width: 270px;
  margin-bottom: 40px;

  cursor: pointer ;
  transition: transform 400ms;
  
}

.play_image{
  width: 250px;
  height: 140px;
 
}

.video_play{
  display: flex;
  flex-wrap: wrap;
}
.play_avatar{
  width: 30px !important ;
  height: 30px !important;
 
}
.play:hover{
  
  transform: scale(1.08) ;
}

.play_info{
  display: flex;
  margin-top: 10px;
  padding-right: 30px;
}

.play_text{
  margin-left: 10px;
}

.play_text>h4{
  font-size: 14px;
  margin-bottom: 5px;
}

.play_text>p{
  font-size: 14px;
  color: gray;
}

.navbar_menuicon{
  display: none;
}

@media screen and (max-width: 960px) {
  .navbar_menuicon{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    padding-right: 30px;
    cursor: pointer;
  }
  .nav_icon{
    font-size: xx-large !important;

  }
  .h_right{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 40vh;
    position: absolute; 
    top: 45px;
    right: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }
  .h_right.active{
    background-color: #fff;
    right: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;

  }
  .hr_icon{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    width: 100%;
  }
  .hr_icon:hover{
    background-color: #1888ff;
    transition: 250ms;

  }
}